import React from 'react';

const LoggerMiddleware = store => next => action => {
	//     
	console.log(' ', store.getState());

	//  
	console.log('', action);

	//      
	const result = next(action);

	//      .
	console.log(' ', store.getState());
	console.log('\n');

	return result;
};

export default LoggerMiddleware;



